import React, {Component, Fragment} from 'react'
import {Redirect} from 'react-router-dom'
import {Query, Mutation} from 'react-apollo'
import ListRentalComponent from './ListRentalComponent'
import AddressComponent from './AddressComponent'
import gql from 'graphql-tag'
const GETUSER = gql`
    query GetUser{
        getUser{
            firstname
            lastname
            email
        }
    }
`
class ListRental extends Component {
    constructor(props){
        super(props)
        this.state = {
            addressEntered: false,
            address: {}
        }
    }
    handleAddress = (values) => {
        this.setState({address: values, addressEntered: true})
    }
    render(){
       return(
           <Query query={GETUSER}>
            {({data,loading,error}) => {
                if(loading){
                    return <div></div>
                }
                if(error){
                    return <Redirect to="/" />
                }
                if(this.state.addressEntered){
                    return (
                        <div>
                            <ListRentalComponent values={data.getUser} address={this.state.address}/>
                        </div>
                    )
                }
                return(
                    <div>
                        <AddressComponent sendAddress={this.handleAddress}/>
                    </div>
                )


            }}

           </Query>
       )
    }
}
export default ListRental